"use client";

import Link from "next/link";
import { useState } from "react";

export default function RegisterPage() {
  const [form, setForm] = useState({
    name: "",
    email: "",
    password: "",
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    alert(`Usuario ${form.name} registrado correctamente (modo prueba)`);
  };

  return (
    <main className="min-h-screen flex flex-col md:flex-row">
      {/* Sección izquierda */}
      <section className="bg-[#FFD400] text-gray-900 w-full md:w-1/2 flex flex-col justify-center items-center p-10">
        <h1 className="text-3xl font-extrabold mb-3">¿Quién Da Más?</h1>
        <h2 className="text-4xl font-bold mb-3">¡Hola, bienvenido!</h2>
        <p className="text-lg text-gray-700 mb-6 text-center">
          Sube artículos a subasta y dales una segunda vida a tus artículos.
        </p>

        <Link
          href="/home"
          className="bg-blue-600 hover:bg-blue-500 text-white px-6 py-3 rounded-lg font-semibold transition"
        >
          EMPEZAR A SUBASTAR
        </Link>
      </section>

      {/* Sección derecha */}
      <section className="w-full md:w-1/2 flex flex-col justify-center items-center p-10 bg-white relative">
        <form
          onSubmit={handleSubmit}
          className="w-full max-w-sm bg-white rounded-lg p-6 shadow-md border border-gray-100"
        >
          <h2 className="text-2xl font-bold text-gray-800 text-center mb-6">
            Crear cuenta
          </h2>

          <input
            type="text"
            name="name"
            placeholder="Nombre completo"
            onChange={handleChange}
            required
            className="w-full p-3 mb-4 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-yellow-300"
          />

          <input
            type="email"
            name="email"
            placeholder="Correo electrónico"
            onChange={handleChange}
            required
            className="w-full p-3 mb-4 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-yellow-300"
          />

          <input
            type="password"
            name="password"
            placeholder="Contraseña"
            onChange={handleChange}
            required
            className="w-full p-3 mb-4 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-yellow-300"
          />

          <button
            type="submit"
            className="w-full bg-blue-600 hover:bg-blue-500 text-white py-3 rounded-lg font-semibold transition"
          >
            REGISTRARME
          </button>

          <p className="text-center text-gray-600 text-sm mt-4">
            ¿Ya tienes cuenta?{" "}
            <Link href="/login" className="text-blue-600 hover:underline">
              Inicia sesión
            </Link>
          </p>
        </form>

        {/* Footer */}
        <div className="mt-10 flex gap-4 text-gray-500 text-sm">
          <span>Síguenos</span>
          <a href="#" className="hover:text-blue-600">Facebook</a>
          <a href="#" className="hover:text-blue-600">Twitter</a>
          <a href="#" className="hover:text-blue-600">Instagram</a>
        </div>
      </section>
    </main>
  );
}
